import "./assets/css/chooser.css!";
import jquery  = require("jquery");
import {$http} from "../http/http";
import {EInputEvent} from "../component/enum/e-input-event";
import {EMouseEvent} from "../component/enum/e-mouse-event";
import {ABaseInput} from "./abstract/a-base-input";
import {IInputAddon} from "./interface/i-input-addon";
import {InputAddon} from "./input-addon";
import {EventEmitter} from "../core/event-emitter";

export interface ISelectOptions {
	url?:string;
	listPosition?:"top"|"bottom";
	placeholder?: string;
	minSearchLength?:number;
	multiple?:boolean;
	searchIgnoreCase?:boolean;
	clearOnEmptyTerm?:boolean;
	searchDelayTime?:number;
	autocomplete?:boolean;
	searchSimbol?:string;
}

export interface IParamsSearch {
	url?:string;
	params?:any;
}

export class Chooser<T> extends ABaseInput<T>{
	private config: ISelectOptions = {};
	private data:T[];
	private itemSelected:T;
	private url:string;
	private params:IParamsSearch;
	private fnDisplayField:(item:T) => string;
	private fnDisplayListItem:(item:T) => string;
	private fnParams:() => any;
	private descriptionField:string;
	private searchFields:string[];
	private assignedProperty: string;
	private lastTextSearch: string;
	private btnClear:InputAddon;
	public onSelect: EventEmitter<T[]> = new EventEmitter<T[]>();

	constructor(config?:ISelectOptions){
		super("div", "");
		this.data = [];
		this.params = {};
		this.url = config ? config.url:null;
		this.config = Object.assign({},{minSearchLength:0,listPosition:"bottom"},config||{});
		this.fnDisplayField = item => this.getValueByPath(item,this.getDescriptionField());
		this.fnDisplayListItem = item => this.getValueByPath(item,this.getDescriptionField());
		
		this.render();

		this.btnClear = new InputAddon().setIcon("mv-basico-fechar").setCssProperties({"font-size":"15px","padding-bottom":"0px","padding-top":"1px","line-height":"0px"});
		this.btnClear.addEvent(EMouseEvent.CLICK,()=>{
			this.setValue(null);
			this.getMenuListItem().hide();
		});
		this.insertAddon("append", this.btnClear);
		this.lastTextSearch = "";
		
	}

	protected insertAddon(method: string, addon: IInputAddon): this {
		let $container = jquery(this.element).children("div.input-group");
		$container[method](addon.element);
		return this;
	}

	private init() {
		this.setList(this.data);
	}
	private fakeFocus(on:boolean):this{        
		if(on){
			this.getDisplayItem().addClass("is-focus");
		}else{
			this.getDisplayItem().removeClass("is-focus");
		}        
		return this;
	}

	private render() {
		let $ = jquery(this.element);
		let $this = this;

		$.addClass(`Chooser form-group col-xs-12 col-sm-4 col-md-4 is-list-${this.config.listPosition}`);
		$.append(`
			<label class="control-label" for="input_${this._uid}" style="display:none"></label>
			<div class="col-xs-12 input-group input-group-sm">
				<div class="form-control display-item-input"></div>
				<input class="form-control" style="display:none" id="input_${this._uid}" autocomplete="off">
				<ul class="options-container" style="display:none" id="input_${this._uid}_"></ul>
			</div>
		`);

		this.getMenuListItem().on("click","li.display-item-list",function (){
			let dataIndex =  jquery(this).attr("data-index");
			$this.itemSelected = $this.data[dataIndex];
			$this.setValue($this.itemSelected);
			$this.getMenuListItem().hide();
			$this.getInput().focus();
			$this.onSelect.emit([$this.itemSelected]);
		}).on("mouseover",() => {
			jquery($this.getMenuListItem()).addClass("is-over");
			jquery($this.getMenuListItem()).show();
		}).on("mouseleave",() => {
			jquery($this.getMenuListItem()).removeClass("is-over");
			if(!jquery($this.getInput()).hasClass("is-over")){
				jquery($this.getMenuListItem()).hide();
			}
		});

		this.getInput().on("keyup",(evt:any)=>{
			if(!$this.isReadonly()){
				
				let txtSearch:string = $this.getInput().val();
				let tmpContainer = $this.getMenuListItem();
				if(txtSearch && txtSearch.length >= this.config.minSearchLength && txtSearch !== this.lastTextSearch){
					this.lastTextSearch = txtSearch;
					let paramSearch:{} = {};
					if(this.descriptionField){
						paramSearch[this.descriptionField] = txtSearch.toUpperCase();
					}else{
						paramSearch["descricao"] = txtSearch.toUpperCase();
						if($this.searchFields && $this.searchFields.length){
							$this.searchFields.forEach(field=>{
								paramSearch[field] = txtSearch.toUpperCase();
							});
						}
					}

					if((evt.which >= 35 && evt.which <= 40) 
					|| (evt.which >= 16 && evt.which <= 18)  
					|| evt.which === 46){
						evt.preventDefault();
						evt.stopPropagation();
						let lastSelected = tmpContainer.find("li.display-item-list.is-active:first");
						tmpContainer.find("li.display-item-list.is-active").removeClass("is-active");
						if(evt.which === 38){
							//up
							if(lastSelected.hasClass("is-first")){
								lastSelected.addClass("is-active");
							}else if(lastSelected.length){                       
								lastSelected.prev().addClass("is-active");
							}else{
								tmpContainer.find("li.display-item-list:last").addClass("is-active");
							} 
						}else if(evt.which === 40){
							//down
							if(lastSelected.hasClass("is-last")){
								lastSelected.addClass("is-active");
							}else if(lastSelected.length){                       
								lastSelected.next().addClass("is-active");
							}else{
								tmpContainer.find("li.display-item-list:first").addClass("is-active");
							}                   
						}else{
							$this.load({params:paramSearch});
							if(this.data && this.data.length){
								jquery(tmpContainer).show();
							}
						}
						return false;
					}else if(evt.which === 13){
						//enter
						let tmpItem = tmpContainer.find("li.display-item-list.is-active:first");
						if(tmpItem.length){                    
							let dataIndex =  tmpItem.attr("data-index");
							$this.itemSelected = $this.data[dataIndex];
							if($this.itemSelected){
								$this.setValue($this.itemSelected);
								$this.getMenuListItem().hide();
								$this.getInput().focus();
								$this.onSelect.emit([$this.itemSelected]);
							}
						}
						return;
					}else if(!(evt.which === 9)){
						//pesquisa de texto normal
						$this.load({params:paramSearch}).then(()=>{
							if(this.data && this.data.length){
								jquery(tmpContainer).show();
							}
						});
					}

				}else{
					jquery(tmpContainer).hide();
				}
			}
		}).on("blur",(evt:Event)=>{
			if(!$this.getInput().val()){
				$this.getInput().hide();
				$this.getDisplayItem().show();
			}
			if(!jquery($this.getMenuListItem()).hasClass("is-over")){
				jquery($this.getMenuListItem()).hide();
			}
		}).on("mouseover",()=>{
			jquery($this.getInput()).addClass("is-over");
		}).on("mouseout",()=>{
			jquery($this.getInput()).removeClass("is-over");
		}).on("click",()=>{
			let txtSearch:string = $this.getInput().val();
			if(txtSearch && !this.data){
				let paramSearch:{} = {};
				if(this.descriptionField){
					paramSearch[this.descriptionField] = txtSearch.toUpperCase();
				}else{
					paramSearch["descricao"] = txtSearch.toUpperCase();
					if($this.searchFields && $this.searchFields.length){
						$this.searchFields.forEach(field=>{
							paramSearch[field] = txtSearch.toUpperCase();
						});
					}
				}
				$this.load({params:paramSearch});
			}
			$this.showList(true);
		});

		jquery(this.getDisplayItem()).on("click",()=>{
			if(!this.isDisabled()){
				$this.getInput().show();
				$this.getDisplayItem().hide();
				$this.getInput().focus();
			}
		});

		this.getDisplayItem().html(`<span class="is-placeholder">Digite no mínimo 2 dígitos</span>`);
	}

	public showList(visible: boolean): this {        
		let input = this.getInput();
		if(input.prop("disabled") || input.prop("readonly")){
			return this;
		}else{
			let menuList = this.getMenuListItem();
			if(visible){
				menuList.show();
			}else{
				menuList.hide();
			}     
		}
		return this;
	}

	public setUrl(url:string){
		this.url = url;
	}

	public getUrl(){
		return this.url;
	}

	public setParams(params:any):this{
		this.params = params;
		return this;
	}

	public setAssignedProperty(field: string): this {
		this.assignedProperty = field;
		return this.data ? this.refresh() : this;
	}

	public getAssignedProperty(): string {
		return this.assignedProperty;
	}

	public getParams():any{
		return this.params;
	}

	public setDescriptionField(field: string): this {
		this.descriptionField = field;
		return this;
	}

	public getDescriptionField(): string {
		return this.descriptionField;
	}

	public setSearchFields(field: string[]): this {
		this.searchFields = field;
		return this;
	}

	public getSearchFields(): string[] {
		return this.searchFields;
	}

	private getDisplayItem():JQuery{
		return jquery(this.element).find(".input-group:first > .display-item-input:first");
	}

	public setPlaceholder(placeholder: string): this {
		this.config.placeholder = placeholder;
		this.getInput().attr("placeholder",placeholder);
		this.getDisplayItem().html(`<span class="is-placeholder">${placeholder}</span>`);
		return this;
	}

	public setData(data:T[]):this{
		this.data = data;
		this.refresh();
		return this;
	}

	public getData():T[]{
		return this.data;
	}

	public setValue(item: T): this {
		this.itemSelected = item;

		if(this.itemSelected){
			this.lastTextSearch = this.fnDisplayField(this.itemSelected);
			this.getInput().val(this.fnDisplayField(this.itemSelected));
			this.getInput().show();
			this.getDisplayItem().hide();
		}else{
			this.lastTextSearch = "";
			this.getInput().val("");
			this.getInput().hide();
			this.getDisplayItem().show();
		}
		if(this.isDirty()){
			this.fireEvent(EInputEvent.CHANGE);
		}    
		return this;
	}

	public getValue(): T {
		return this.itemSelected;       
	}

	public refresh(): this {
		this.init();
		return this;
	}

	public load(configLoad?:IParamsSearch):Promise<T[]>{
		let url = configLoad && configLoad.url?configLoad.url:this.url;
		let params = configLoad && configLoad.params?Object.assign({},configLoad.params,this.params||{}):this.params;
		return $http.get(url).params(params).then((data:T[])=>{
			this.setData(data);
			return data;
		}).catch(()=>{
			this.setData([]);
			return [];
		});
	}

	private getMenuListItem():JQuery{
		return jquery(this.element).find("ul:first");
	}

	private setList(data:T[]):this{
		let $_this = this;
		let ulItens = this.getMenuListItem();
		let innerHTML = "";
		let tmList:number = data.length;     
		data.forEach((dta,indx)=>{
			let positionInd:string = (indx === 0 ? "is-first":"");
			if(!positionInd){
				positionInd = (indx+1 === tmList ? "is-last":"");
			}
			innerHTML += `<li data-index="${indx}" class="${positionInd} display-item-list">
				${this.fnDisplayListItem(dta)}    
			</li>`;
		});
		innerHTML+=`<li class="no-result-display is-hidden"></li>`;
		ulItens.html(innerHTML);
		return this;
	}

	public setDisplayItem(display:(item:T)=>string):this{
		this.fnDisplayField = display;
		return this;
	}

	public setDisplayListItem(display:(item:T)=>string):this{
		this.fnDisplayListItem = display;
		return this;
	}

	protected getInput(): JQuery {
		return jquery(this.element).find("input:first");
	}

	private getValueByPath(item:any,path:string){
		let valueGroup = item;
		if(path.indexOf(".") > -1){
			valueGroup = path.split(".").reduce( function ( prev, curr ) {
								return prev ? prev[curr]:null;
							}, item || this );
		}else{
			valueGroup = item?item[path]:null;
		}
		return valueGroup;
	}
}
